import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonService } from 'src/app/common.service';
import { AdminService } from '../../admin.service';
declare var $: any;

@Component({
  selector: 'app-admin-edit-profile',
  templateUrl: './admin-edit-profile.component.html',
  styleUrls: ['./admin-edit-profile.component.css']
})
export class AdminEditProfileComponent implements OnInit {
  adminInfo: any;
  myGroup: any;
  public eventObj: any = { event_image: 'assets/images/upload.png' }
  file: any;
  public disableSubmit = false;
  public loading = false;
  public error = false;
  constructor(private activateRoute: ActivatedRoute, private route: Router, private adminservices: AdminService, private formBuilder: FormBuilder) {
    this.myGroup = formBuilder.group({
      name: ['', Validators.required],
      email: ['', Validators.compose([Validators.required, Validators.pattern(/^[A-Z0-9_-]+([\.-][A-Z0-9_-]+)*@[A-Z0-9-]+(\.[a-zA-Z]{2,4})+$/i)])],
      image: ['']
    });
  }
  ngOnInit() {

    $('input').on('paste', function (event) {
      if (event.originalEvent.clipboardData.getData('Text').match(/[^\d]/)) {
        event.preventDefault();
      }
    });
    this.getProfileDetail();
    this.activeClass()
  }
  getProfileDetail() {
    this.adminservices.getApi(`admin/getProfile`, 1).subscribe(response => {
      if (response['code'] == 200) {
        this.adminInfo = response['result']
        this.myGroup.patchValue({
          name: this.adminInfo.name,
          email: this.adminInfo.email,
        })
      } else {

      }
    }, error => {

    })
  }

  editProfile(val) {
    // stop here if form is invalid
    if (this.myGroup.invalid) {
      return;
    }

    this.error = false;
    this.disableSubmit = true;
    this.loading = true;
    let formdata = new FormData;
    formdata.append('file', this.file)
    formdata.append('name', val.name)
    formdata.append('email', val.email);

    this.adminservices.postApi(`admin/editProfile`, formdata, 1).subscribe(response => {
      this.disableSubmit = false;
      this.loading = false;
      if (response['code'] == 200) {
        this.myGroup.reset();
        this.getProfileDetail()
        this.route.navigate(['profile']);

        //Set Data in Local Storage
        if (response['result'] != {}) {
          localStorage.removeItem("admin_user");
          let enc = JSON.stringify(response['result']);
          localStorage.setItem('admin_user', this.adminservices.encryptData(enc));
        }
      }
      else {
        this.error = true;
      }
    }, error => {
      this.error = true;
    })
  }

  onFileChanged(evt: any) {
    this.file = evt.target.files[0]
  }
  activeClass() {
    $(".sidebarprofile").addClass("active"); // instead of this do the below 
    $(this).addClass("active");
  }
  paste(event) {
    if (event.clipboardData.getData('Text').match(/[^\d]/)) {
      event.preventDefault();
    }
  }
  alphabat(event: any) {
    const pattern = /[a-zA-Z\ ]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
}
