import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { CommonService } from '../../common.service';
import { MustMatch } from './MustMatch';
import { Router } from '@angular/router';
import { AdminService } from '../../admin.service';

declare var $: any
@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.css']
})
export class ChangePasswordComponent implements OnInit {
  myGroup: any;
  submitted = false;
  public disableSubmit = false;
  public loading = false;
  public error = false;
  constructor(private _fb: FormBuilder, private commonService: CommonService, private adminservices: AdminService, private router: Router) {
    this.myGroup = this._fb.group({
      oldPassword: ['', [Validators.required, Validators.minLength(6)]],
      newPassword: ['', [Validators.required, Validators.minLength(6)]],
      confirmPassword: ['', [Validators.required]]
    }, {
      validator: MustMatch('newPassword', 'confirmPassword')
    })
  }

  ngOnInit() {
  }
  get f() { return this.myGroup.controls; }

  newData() {
    this.error = false;
  }

  changePassword(val) {
    this.submitted = true;

    this.error = false;
    this.disableSubmit = true;
    this.loading = true;

    let temp = {
      "password": val.oldPassword,
      "new_password": val.newPassword
    }

    this.adminservices.postApi(`admin/changePassword`, temp, 1).subscribe(response => {
      this.disableSubmit = false;
      this.loading = false;
      if (response['code'] == 200) {
        this.router.navigate(['/profile'])
      }
      else {
        this.error = true;
      }
    }, error => {
      this.error = true;
    })
  }
}
