import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { VendorAuthGuard } from '../vendor-auth-guard.service';
import { CanDeactivateGuard } from '../shared/guard/can-deactivate-guard.service';

import { AdminEditProfileComponent } from './admin-edit-profile/admin-edit-profile.component';
import { AdminProfileComponent } from './admin-profile/admin-profile.component';
import { ChangePasswordComponent } from './change-password/change-password.component';

const routes: Routes = [
  { path: "", component: AdminProfileComponent },
  { path: "edit", component: AdminEditProfileComponent },
  { path: "change-password", component: ChangePasswordComponent },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProfileTabRoutingModule { }
